// pages/comic_detail/comicDetail.js
var localOverView = require('../../data/comic_detail.js');
var localDetailTab1 = require('../../data/comic_detail_tab1.js');
var localDetailTab2 = require('../../data/comic_detail_tab2.js');
var localDetailTab3 = require('../../data/comic_detail_tab3.js'); //获取应用实例
//获取应用实例
const app = getApp();

export default {
    data() {
        return {
            id: 0,
            overView: {},
            detailTab1: {},
            detailTab2: {},
            detailTab3: {},
            // 这里改变当前显示第几个tab
            currentTab: 1
        };
    },

    /**
     * 用户点击右上角分享
     */
    onShareAppMessage: function() {},

    /**
     * 页面上拉触底事件的处理函数
     */
    onReachBottom: function() {},

    /**
     * 页面相关事件处理函数--监听用户下拉动作
     */
    onPullDownRefresh: function() {},

    /**
     * 生命周期函数--监听页面卸载
     */
    onUnload: function() {},

    /**
     * 生命周期函数--监听页面隐藏
     */
    onHide: function() {},

    /**
     * 生命周期函数--监听页面显示
     */
    onShow: function() {},

    /**
     * 生命周期函数--监听页面初次渲染完成
     */
    onReady: function() {},

    /**
     * 生命周期函数--监听页面加载
     */
    onLoad: function(options) {
        // 页面初始化 options为页面跳转所带来的参数
        if (options.id) {
            this.setData({
                id: parseInt(options.id)
            });
        }

        this.setData({
            overView: localOverView,
            detailTab1: localDetailTab1,
            detailTab2: localDetailTab2,
            detailTab3: localDetailTab3
        });
        console.log(this.detailTab1);
        uni.setNavigationBarTitle({
            title: localOverView.title
        });
    },
    components: {},
    props: {},
    methods: {
        // tab滑动
        bindChange(e) {
            this.setData({
                currentTab: e.detail.current
            });
        },

        // 左
        onLeft() {
            this.setData({
                currentTab: 0
            });
        },

        // 、中
        onMiddle() {
            this.setData({
                currentTab: 1
            });
        },

        // 右
        onRight() {
            this.setData({
                currentTab: 2
            });
        },

        setData: function(obj) {
            let that = this;
            let keys = [];
            let val, data;
            Object.keys(obj).forEach(function(key) {
                keys = key.split('.');
                val = obj[key];
                data = that.$data;
                keys.forEach(function(key2, index) {
                    if (index + 1 == keys.length) {
                        that.$set(data, key2, val);
                    } else {
                        if (!data[key2]) {
                            that.$set(data, key2, {});
                        }
                    }

                    data = data[key2];
                });
            });
        }
    }
};
